<template>
	<div>
		<a-drawer title="" width="520" :closable="false" :visible="visible" @close="onClose">
			<a-list item-layout="horizontal" :data-source="dataSource">
				<a-list-item slot="renderItem" slot-scope="item, index">
					<a-list-item-meta :description="item.content">
						<a slot="title" @click="showChildrenWindow(item)">{{ item.adminName }}</a>
						<a-avatar slot="avatar" :src="picRequestPath + 'headpic/' + item.headPic" />
					</a-list-item-meta>
					<a slot="actions" v-if="item.hasRead==0"></a>
					<span slot="actions" v-else></span>
				</a-list-item>
			</a-list>
			<div style="margin-top: 10px;">
				<a-button type="primary" @click="allRead" :disabled="!buttonUsable">
					
				</a-button>
			</div>
			<a-modal v-model="childrenDrawer" title="" @ok="handleOk" @cancel="handleCancel">
				<p style="font-size: 16px;" v-if="msg.adminEmail"> From: {{msg.adminName}} ({{msg.adminEmail}})</p>
				<p style="font-size: 16px;" v-else> From: {{msg.adminName}}</p>
				<p style="font-size: 16px;"> Time: {{msg.createdTime}}</p>
				<p style="font-size: 16px;"> Msg: {{msg.content}}</p>
			</a-modal>
		</a-drawer>
	</div>
</template>

<script>
	import {
		Modal
	} from 'ant-design-vue';
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		props: ['mopen', 'msgNum'],
		async created() {
			this.allMsg();
			if (this.msgNum == 0) {
				this.buttonUsable = false;
			} else {
				this.buttonUsable = true;
			}
			this.visible = this.mopen;		
		},
		computed: {
			...mapState(['requestPath', 'userInfo','picRequestPath']),
		},
		data() {
			return {
				buttonUsable: true,
				visible: false,
				childrenDrawer: false,
				msg: {
					adminName: 'Ant Design Title 1',
					headPic: 'default.jpg',
					content: 'enennenenenene',
					createdTime: '2020-1-1',
					hasRead: 0,
				},
				dataSource: null,
			};
		},
		methods: {
			async allMsg() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'bulletin/user/' + this.userInfo.uid);
				if (data.code == 0) {
					this.dataSource = data.data;
				}
			},
			async allRead() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'bulletin/user/read_all/' + this.userInfo.uid);
				if (data.code == 0) {
					for (var i = 0; i < this.dataSource.length; i++) {
						this.dataSource[i].hasRead = 1;
					}
					this.buttonUsable = false;
				}
			},
			async readThis() {
				let params = {
					uid: this.userInfo.uid,
					bid: this.msg.id,
				};
				let {
					data
				} = await this.axios.post(this.$store.state.requestPath + 'bulletin/user/read_this' , params);
			}, 
			showDrawer() {
				this.visible = true;
			},
			onClose() {
				this.$emit("closeMsgWindow");
				this.visible = false;
			},
			showChildrenWindow(item) {
				this.msg = item;
				this.childrenDrawer = true;
				this.readThis();
			},
			handleOk(e) {
				this.childrenDrawer = false;
				this.allMsg();
			},
			handleCancel(e){
				this.childrenDrawer = false;
				this.allMsg();
			}
		},
	};
</script>

