<template>
	<a-layout id="components-layout-demo-fixed">
		<!-- <a-layout-header :style="{ position: 'fixed', zIndex: 1, width: '100%' }"> -->
		<a-layout-header :style="{zIndex: 1, width: '100%' }">
			<div class="logo" />
			<a-menu theme="dark" mode="horizontal" :default-selected-keys="[title]" :style="{ lineHeight: '64px'}">
				<!-- <a-menu-item :key=i v-for="i in 3" @click="navMove(i)">nav {{i}}</a-menu-item> -->
				<a-menu-item key="">
					<router-link to="/home/nav1">
						<a-icon type="project" theme="filled" /><span></span></router-link>
				</a-menu-item>
				<a-menu-item key="">
					<router-link to="/home/nav2">
						<a-icon type="video-camera" theme="filled" /><span></span></router-link>
				</a-menu-item>
				<a-menu-item disabled>
					<a-input-search placeholder="..." size="large" @search="onSearch" style="margin: 12px 0 0 20px;">
						<a-button slot="enterButton">
							<a-icon type="search" />
						</a-button>
					</a-input-search>
					<SearchResultPage v-if="sopen" :sopen="sopen" :value="value" @closeResWindow="closeResWindow"></SearchResultPage>
				</a-menu-item>
				<!-- null -->
				<a-dropdown :style="{float: 'right', 'margin-right':'-10px', 'fontSize':'18px'}" placement="bottomRight" size="large"
				 v-if="userInfo">
					<a class="ant-dropdown-link" @click="e => e.preventDefault()">
						<a-badge :count="msgNum">
							<a-avatar shape="square" :src="picRequestPath + 'headpic/' +userInfo.headPic" :alt="userInfo.name" />
						</a-badge><span style="margin-left: 20px;">{{userInfo.name}}</span>
						<!--
						<a-icon type="user" /> 
						<a-icon type="down" /> -->
					</a>
					<a-menu slot="overlay">
						<a-menu-item @click="showHistoryViewWindow()">
							<a-icon type="reload" /><span style="margin-left: 5px;"></span>
							<HistoryViewPage v-if="hopen" :hopen="hopen" @closeHistoryViewWindow="closeHistoryViewWindow"></HistoryViewPage>
						</a-menu-item>
						<a-menu-item @click="showMsgWindow()">
							<a-icon type="sound" /><span style="margin-left: 5px;"></span>
							<MsgPage v-if="mopen" :mopen="mopen" :msgNum="msgNum" @closeMsgWindow="closeMsgWindow"></MsgPage>
						</a-menu-item>
						<a-menu-item>
							<router-link to="/home/user_info">
								<a-icon type="edit" /><span style="margin-left: 10px;"></span>
							</router-link>
						</a-menu-item>
						<a-menu-item>
							<a @click="openRewtPwdWindow"><a-icon type="tool" /><span style="margin-left: 10px;"></span></a>
							<ResetPwdPage v-if="pwd" :pwd="pwd" @closeResetPwdPage="closeResetPwdPage"></ResetPwdPage>
						</a-menu-item>
						<a-menu-item @click="userLogout()">
							<a-icon type="poweroff" /><span style="margin-left: 5px;"></span>
						</a-menu-item>
					</a-menu>
				</a-dropdown>
				<a-menu-item :style="{float: 'right', 'margin-right':'-25px'}" @click="userLogout()" v-else>
					<a-icon type="login" />&nbsp;&nbsp; 
				</a-menu-item>
			</a-menu>
		</a-layout-header>

		<router-view></router-view>
		<a-layout-footer :style="{ textAlign: 'center' }">
			Have a Nice Day! --Ant Design 2020 Created--
		</a-layout-footer>
	</a-layout>
</template>

<script>
	import MsgPage from "./MsgPage.vue";
	import ResetPwdPage from "./ResetPwdPage.vue";
	import HistoryViewPage from "./HistoryViewPage.vue";
	import SearchResultPage from "./SearchResultPage.vue";
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		components: {
			MsgPage,
			ResetPwdPage,
			HistoryViewPage,
			SearchResultPage
		},
		data() {
			return {
				value: null,
				sopen: false,
				hopen: false,
				mopen: false,
				pwd: false,
				msgNum: 0,
			};
		},
		async created() {
			this.getMsgNum();
		},
		computed: {
			...mapState(['requestPath', 'userInfo','picRequestPath']),
			title() {
				return this.$route.meta.title;
			},
		},
		methods: {
			...mapMutations(['logout']),
			async getMsgNum() {
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'msg_num/' + this.userInfo.uid);
					this.msgNum = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			userLogout() {
				this.logout();
				this.$router.push({
					path: '/'
				});
				this.$message.success(' () ~ ', 2);
			},
			onSearch(value) {
				if (value.length == 0 || value == null || value.replace(/(^s*)|(s*$)/g, "").length == 0) {
					console.log("");
					return;
				}
				this.value = value;
				this.sopen = true;
			},
			closeResWindow() {
				this.sopen = false;
			},
			showHistoryViewWindow() {
				this.hopen = true;
			},
			closeHistoryViewWindow() {
				this.hopen = false;
			},
			showMsgWindow() {
				this.mopen = true;
			},
			closeMsgWindow() {
				this.mopen = false;
				this.getMsgNum();
			},
			openRewtPwdWindow() {
				this.pwd = true;
			},
			closeResetPwdPage() {
				this.pwd = false;
			}
		}
	}
</script>

<style lang="scss">
	#components-layout-demo-fixed {
		background: rgba(255, 255, 255, 0.2);

		.logo {
			width: 120px;
			height: 31px;
			background: rgba(255, 255, 255, 0.2);
			margin: 16px 24px 16px 0;
			float: left;
		}
	}

	#components-badge-demo-dot .anticon-notification {
		width: 16px;
		height: 16px;
		line-height: 16px;
		font-size: 1px;
	}
</style>
