<template>
	<div>
		<a-drawer title="" width="520" :closable="false" :visible="visible" @close="onClose">
			<a-list item-layout="horizontal" :data-source="dataSource">
				<a-list-item slot="renderItem" slot-scope="item, index">
					<a-list-item-meta :description="item.intro">
						<a slot="title" @click="linkToMovie(item.mid)">{{ item.name }}</a>
						<a-avatar slot="avatar" shape="square" :size="100" :src="picRequestPath + 'poster/' + item.pic" />
					</a-list-item-meta>
				</a-list-item>
			</a-list>
		</a-drawer>
	</div>
</template>

<script>
	import {
		Modal
	} from 'ant-design-vue';
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		props: ['sopen', 'value'],
		async created() {
			this.getSearchData();
			this.visible = this.sopen;
		},
		computed: {
			...mapState(['requestPath', 'userInfo','picRequestPath']),
		},
		data() {
			return {
				visible: false,
				dataSource: [],
			};
		},
		methods: {
			async getSearchData() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'search/' + this.value);

				if (data.code == 0) {
					this.dataSource = data.data;
				}
			},
			onClose() {
				this.$emit("closeResWindow");
				this.visible = false;
			},
			linkToMovie(mid) {
				this.onClose();
				this.$router.push({
					path: '/home/movie_detail',
					query: {
						'mid': mid
					}
				});
			}
		},
	};
</script>

