<template>
	<div>
		<a-drawer title="" width="520" :closable="false" :visible="visible" @close="onClose">
			<a-list item-layout="horizontal" :data-source="dataSource">
				<a-list-item slot="renderItem" slot-scope="item, index">
					<a-list-item-meta :description="item.intro">
						<a slot="title" @click="linkToMovie(item.mid)">{{ item.name }}</a>
						<a-avatar slot="avatar" shape="square" :size="100" :src="picRequestPath + 'poster/' + item.pic" />
					</a-list-item-meta>
					<a slot="actions" @click="del(item.mid)"></a>
				</a-list-item>
			</a-list>
			<div style="margin-top: 10px;">
				<a-button type="primary" @click="allDel" :disabled="dataSource.length>0?false:true">
					
				</a-button>
			</div>
		</a-drawer>
	</div>
</template>

<script>
	import {
		Modal
	} from 'ant-design-vue';
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		props: ['hopen'],
		async created() {
			this.allHistory();
			this.visible = this.hopen;		
		},
		computed: {
			...mapState(['requestPath', 'userInfo','picRequestPath']),
		},
		data() {
			return {
				visible: false,
				dataSource: [],
			};
		},
		methods: {
			async allHistory() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'history_view/' + this.userInfo.uid);
				if (data.code == 0) {
					this.dataSource = data.data;
				}
			},
			onClose() {
				this.$emit("closeHistoryViewWindow");
				this.visible = false;
			},
			async del(mid){
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'history_view/del/'+ mid +'/' + this.userInfo.uid);
				if (data.code == 0) {
					this.$message.success("");
					this.allHistory();
				}else{
					this.$message.error("")
				}
			},
			async allDel(){
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'history_view/del_all/' + this.userInfo.uid);
				if (data.code == 0) {
					this.$message.success("");
					this.allHistory();
				}else{
					this.$message.error("")
				}
			},
			linkToMovie(mid){
				this.onClose();
				this.$router.push({path:'/home/movie_detail', query:{'mid': mid}});
			}
		},
	};
</script>

